.submenu{
  display: none;
  margin: 10px;
  padding: 20px;
  position: absolute;
  background-color: rgb(243, 238, 234);
  width: 130px;
  
}

ul{
  cursor: pointer;
  width: 100%;
  display: flex;
  margin-bottom: 30px;
}
li{
  padding: 10px;
  margin: 10px;
  list-style: none;

}
td{
  padding: 20px;
  border: 1px solid rgb(62, 62, 62);
}
form{
  width: 100%;
  background-color: rgba(250, 191, 114, 0.64);
  padding: 10px;
  text-align: center;
}
form input {
  padding: 10px;
  justify-content:space-evenly ;
  width: 10%;
}
